<!-- RightButtonGroup.vue -->
<template>
    <div class="right-button-group">

        <el-button type="primary" class="long-btn2" v-if="isShowZhongYangButton"
            :class="{ active: selectedButton === '中央空调' }" @click="handleZhongYangClick">
            <i class="el-icon-office-building"></i>
            中央空调
        </el-button>

        <el-button type="primary" class="long-btn" v-if="isShowDeviceInfoButton"
            :class="{ active: selectedButton === '设备信息' }" @click="handleDeviceInfoClick">
            <i class="el-icon-info"></i>
            设备信息
        </el-button>

        <el-button type="primary" class="long-btn3" v-if="isDataRelatedButton" :class="{ active: selectedButton === '数据总览' }"
            @click="handleDataOverviewClick">
            <i class="el-icon-data-analysis"></i>
            数据总览
        </el-button>

        <el-button type="primary" class="long-btn4" v-if="isDataRelatedButton" :class="{ active: selectedButton === '用电计量' }"
            @click="handleElectricityMeterClick">
            <i class="el-icon-lightning"></i>
            用电计量
        </el-button>

        <el-button type="primary" class="long-btn5" v-if="isDataRelatedButton" :class="{ active: selectedButton === '用水计量' }"
            @click="handleWaterMeterClick">
            <i class="el-icon-watermelon"></i>
            用水计量
        </el-button>

        <el-button type="primary" class="long-btn6" v-if="isGJButton" :class="{ active: selectedButton === '告警级别' }"
            @click="handleAlarmLevelClick">
            <i class="el-icon-bell"></i>
            告警级别
        </el-button>

        <el-button type="primary" class="long-btn7" v-if="isGJButton" :class="{ active: selectedButton === '告警规则' }"
            @click="handleAlarmRuleClick">
            <i class="el-icon-setting"></i>
            告警规则
        </el-button>

        <el-button type="primary" class="long-btn8" v-if="isGJButton" :class="{ active: selectedButton === '告警处理' }"
            @click="handleAlarmHandleClick">
            <i class="el-icon-edit"></i>
            告警处理
        </el-button>

        <el-button type="primary" class="long-btn10" v-if="isZhiNengQuanXianButton"
            :class="{ active: selectedButton === '智能控制' }" @click="handleZhiNengClick">
            <i class="el-icon-cpu"></i>
            智能控制
        </el-button>

        <el-button type="primary" class="long-btn9" v-if="isZhiNengQuanXianButton"
            :class="{ active: selectedButton === '权限控制' }" @click="handleQuanXianClick">
            <i class="el-icon-lock"></i>
            权限控制
        </el-button>


    </div>
</template>

<script>
export default {
    data() {
        return {
            localSelectedButton: this.selectedButton
        };
    },
    computed: {
        isShowDeviceInfoButton() {
            return ['GongZuoTai', 'zhongyangkongtiao'].includes(this.$route.name);
        },
        isShowZhongYangButton() {
            return ['GongZuoTai', 'zhongyangkongtiao'].includes(this.$route.name);
        },
        isDataRelatedButton() {
            return ['shujuzonglan', 'yongshuijiliang', 'yongdianjiliang'].includes(this.$route.name);
        },
        isGJButton() {
            return ['gaojingjibie', 'gaojingguize', 'gaojingchuli'].includes(this.$route.name);
        },
        // 新增计算属性控制按钮显示
        isZhiNengQuanXianButton() {
            return ['zhinengkongzhi', 'quanxianguanli'].includes(this.$route.name);
        },
        selectedButton: {
            get() {
                return this.localSelectedButton;
            },
            set(value) {
                this.localSelectedButton = value;
                this.$emit('update:selectedButton', value);
            }
        }
    },
    mounted() {
        this.updateSelectedButtonByRoute();
    },
    watch: {
        '$route.name': function () {
            this.updateSelectedButtonByRoute();
        }
    },
    methods: {
        fetchData() {
            console.log('正在获取数据...');
        },
        updateSelectedButtonByRoute() {
            const routeToButtonMap = {
                'GongZuoTai': '设备信息',
                'zhongyangkongtiao': '中央空调',
                'shujuzonglan': '数据总览',
                'yongdianjiliang': '用电计量',
                'yongshuijiliang': '用水计量',
                'gaojingjibie': '告警级别',
                'gaojingguize': '告警规则',
                'gaojingchuli': '告警处理',
                'quanxianguanli': '权限控制',
                'zhinengkongzhi': '智能控制'
            };

            if (routeToButtonMap[this.$route.name]) {
                this.selectedButton = routeToButtonMap[this.$route.name];
            }
        },
        handleZhongYangClick() {
            this.selectedButton = '中央空调';
            if (this.$route.name !== 'zhongyangkongtiao') {
                this.$router.push({ name: 'zhongyangkongtiao' });
            }
            this.$emit('zhong-yang-click');
        },
        handleDeviceInfoClick() {
            this.selectedButton = '设备信息';
            this.fetchData();
            if (this.$route.name !== 'GongZuoTai') {
                this.$router.push({ name: 'GongZuoTai' });
            }
            this.$emit('device-info-click');
        },
        handleDataOverviewClick() {
            this.selectedButton = '数据总览';
            if (this.$route.name !== 'shujuzonglan') {
                this.$router.push({ name: 'shujuzonglan' });
            }
        },
        handleElectricityMeterClick() {
            this.selectedButton = '用电计量';
            if (this.$route.name !== 'yongdianjiliang') {
                this.$router.push({ name: 'yongdianjiliang' });
            }
        },
        handleWaterMeterClick() {
            this.selectedButton = '用水计量';
            if (this.$route.name !== 'yongshuijiliang') {
                this.$router.push({ name: 'yongshuijiliang' });
            }
        },
        handleAlarmLevelClick() {
            this.selectedButton = '告警级别';
            if (this.$route.name !== 'gaojingjibie') {
                this.$router.push({ name: 'gaojingjibie' });
            }
        },
        handleAlarmRuleClick() {
            this.selectedButton = '告警规则';
            if (this.$route.name !== 'gaojingguize') {
                this.$router.push({ name: 'gaojingguize' });
            }
        },
        handleAlarmHandleClick() {
            this.selectedButton = '告警处理';
            if (this.$route.name !== 'gaojingchuli') {
                this.$router.push({ name: 'gaojingchuli' });
            }
        },
        handleQuanXianClick() {
            this.selectedButton = '权限控制';
            if (this.$route.name !== 'quanxianguanli') {
                this.$router.push({ name: 'quanxianguanli' });
            }
        },
        handleZhiNengClick() {
            this.selectedButton = '智能控制';
            if (this.$route.name !== 'zhinengkongzhi') {
                this.$router.push({ name: 'zhinengkongzhi' });
            }
        }
    }
};
</script>

<style scoped>
.right-button-group {
    position: fixed;
    right: 30px;
    top: 50%;
    transform: translateY(-50%);
    width: 150px;
    z-index: 1000;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    gap: 15px;
}

.long-btn,
.long-btn2,
.long-btn3,
.long-btn4,
.long-btn5,
.long-btn6,
.long-btn7,
.long-btn8,
.long-btn9,
.long-btn10 {
    width: 150px;
    height: 55px;
    border-radius: 28px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    padding: 0 25px;
    background: linear-gradient(135deg, #0c2e5c, #1a4a8f);
    border: 1px solid rgba(64, 158, 255, 0.5);
    box-shadow: 0 0 10px rgba(37, 113, 200, 0.3),
        inset 0 0 10px rgba(37, 113, 200, 0.2);
    transition: all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
    color: #a3cfff;
    font-size: 16px;
    font-weight: 500;
    box-sizing: border-box;
    text-align: left;
    overflow: hidden;
    position: relative;
}

.long-btn::before,
.long-btn2::before,
.long-btn3::before,
.long-btn4::before,
.long-btn5::before,
.long-btn6::before,
.long-btn7::before,
.long-btn8::before,
.long-btn9::before,
.long-btn10::before {
    content: '';
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(90deg, transparent, rgba(64, 158, 255, 0.4), transparent);
    transition: 0.5s;
}

.long-btn:hover::before,
.long-btn2:hover::before,
.long-btn3:hover::before,
.long-btn4:hover::before,
.long-btn5:hover::before,
.long-btn6:hover::before,
.long-btn7:hover::before,
.long-btn8:hover::before,
.long-btn9:hover::before,
.long-btn10:hover::before {
    left: 100%;
}

.long-btn:hover,
.long-btn2:hover,
.long-btn3:hover,
.long-btn4:hover,
.long-btn5:hover,
.long-btn6:hover,
.long-btn7:hover,
.long-btn8:hover,
.long-btn9:hover,
.long-btn10:hover {
    background: linear-gradient(135deg, #1a4a8f, #2a6bc7);
    box-shadow: 0 0 20px rgba(37, 113, 200, 0.6),
        inset 0 0 15px rgba(64, 158, 255, 0.4);
    border-color: rgba(100, 181, 255, 0.8);
    color: #ffffff;
    transform: translateX(-12px);
}

.long-btn.active,
.long-btn2.active,
.long-btn3.active,
.long-btn4.active,
.long-btn5.active,
.long-btn6.active,
.long-btn7.active,
.long-btn8.active,
.long-btn9.active,
.long-btn10.active {
    background: linear-gradient(135deg, #2a6bc7, #3a8df0);
    box-shadow: 0 0 25px rgba(58, 141, 240, 0.8),
        inset 0 0 20px rgba(100, 181, 255, 0.6);
    border: 1px solid rgba(100, 181, 255, 1);
    color: #ffffff;
    font-weight: bold;
}

.long-btn.active::after,
.long-btn2.active::after,
.long-btn3.active::after,
.long-btn4.active::after,
.long-btn5.active::after,
.long-btn6.active::after,
.long-btn7.active::after,
.long-btn8.active::after,
.long-btn9.active::after,
.long-btn10.active::after {
    content: '';
    position: absolute;
    top: -2px;
    left: -2px;
    right: -2px;
    bottom: -2px;
    border-radius: 28px;
    background: linear-gradient(45deg, transparent, #64b5ff, transparent);
    z-index: -1;
    animation: animate 2s linear infinite;
}

@keyframes animate {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}

.long-btn i,
.long-btn2 i,
.long-btn3 i,
.long-btn4 i,
.long-btn5 i,
.long-btn6 i,
.long-btn7 i,
.long-btn8 i,
.long-btn9 i,
.long-btn10 i {
    font-size: 22px;
    margin-right: 12px;
    filter: drop-shadow(0 0 2px rgba(37, 113, 200, 0.5));
}

/* 响应式设计 */
@media (max-height: 800px) {
    .right-button-group {
        gap: 12px;
    }

    .long-btn,
    .long-btn2,
    .long-btn3,
    .long-btn4,
    .long-btn5,
    .long-btn6,
    .long-btn7,
    .long-btn8,
    .long-btn9,
    .long-btn10 {
        width: 140px;
        height: 50px;
        font-size: 15px;
        padding: 0 20px;
    }

    .long-btn i,
    .long-btn2 i,
    .long-btn3 i,
    .long-btn4 i,
    .long-btn5 i,
    .long-btn6 i,
    .long-btn7 i,
    .long-btn8 i,
    .long-btn9 i,
    .long-btn10 i {
        font-size: 20px;
    }
}
</style>